import { Card, CardContent, Typography, Chip, Stack, IconButton, List, ListItem, ListItemText } from "@mui/material";
import InfoIcon from '@mui/icons-material/Info';

export default function WorkExperienceCard({ company, position, tasks, startDate, endDate }) {
    return (
        <Card variant="outlined" sx={{ backgroundColor: "grey.50" }}>
            <CardContent>
                <Stack direction={"row"} spacing={1} alignItems={"center"} sx={{mb: 1}}>
                    <Typography gutterBottom variant="h6">
                        {company}
                    </Typography>
                    <IconButton color="text.primary">
                        <InfoIcon/>
                    </IconButton>
                </Stack>
                <Stack direction={"row"} spacing={1} useFlexGap flexWrap="wrap">
                    <Chip label={position} color="primary" />
                    <Chip label={`${startDate} - ${endDate}`} variant="outlined" />
                </Stack>
            </CardContent>
        </Card>
    )
}