import { Card, CardContent, Link, Stack, Typography } from "@mui/material";

export default function AchievementCard({ title, additional_data, achievement }) {
    return (
        <Card variant="outlined" sx={{ backgroundColor: "grey.50" }}>
            <CardContent>
                <Stack direction={"column"} spacing={1}>
                    <Typography variant="h6">
                        {title}
                    </Typography>
                    {additional_data && Object.keys(additional_data).map((key) => (
                        <Typography variant="body1">{key}: {additional_data[key]} </Typography>
                    ))}
                    <Typography variant="body1">
                        Достижение: {achievement.link == "" ?
                            achievement.name
                            : (
                                <Link href={achievement.link} target="_blank">{achievement.name}</Link>
                            )}
                    </Typography>
                </Stack>
            </CardContent>
        </Card>
    )
}