import { Card, CardContent, Chip, Stack, Typography, CardActions, Button } from "@mui/material";

export default function ProjectCard({ name, description, links, tags }) {
    return (
        <Card variant="outlined" sx={{ backgroundColor: "grey.50" }}>
            <CardContent>
                <Stack direction={"column"} spacing={1}>
                    <Typography variant="h6">
                        {name}
                    </Typography>
                    <Stack direction={"row"} spacing={1} useFlexGap flexWrap="wrap">
                        {tags.map((tag) => (
                            <Chip label={tag} variant="outlined" />
                        ))}
                    </Stack>
                    <Typography variant="body1">
                        {description}
                    </Typography>
                </Stack>
            </CardContent>
            <CardActions>
                {links.map((link) => (
                    <Button variant="outlined" href={link.href} target="_blank">{link.name}</Button>
                ))}
            </CardActions>
        </Card>
    )
}