import { Typography } from "@mui/material"
import CenteredModal from "./CenteredModal"

export default function EducationModal({open, onClose, name, type, profile, startDate, endDate, description}) {
    return (
        <CenteredModal
            open={open}
            onClose={onClose}
        >
            <Typography variant="h6">{name}</Typography>
            <Typography variant="body1"><strong>Тип образования:</strong> {type}</Typography>
            <Typography variant="body1"><strong>Профиль/Специальность:</strong> {profile}</Typography>
            <Typography variant="body1"><strong>Срок обучения:</strong> {startDate} - {endDate}</Typography>
            <Typography variant="body1">{description}</Typography>
        </CenteredModal>
    )
}