import { Grid, Typography } from "@mui/material";
import ProjectCard from "./ProjectCard";
import { Masonry } from "@mui/lab";

export default function Projects() {
    return (
        <Grid container gap={1} direction="column">
            <Typography variant="h5">Проекты</Typography>
            <Masonry container spacing={1} columns={{ md: 2, xs: 1 }}>
                <ProjectCard
                    name="yastation"
                    description="Небольшой CLI-клиент для управления устройствами воспроизведения музыки от Яндкес."
                    links={[
                        { name: "Статья о проекте на habr", href: "https://habr.com/ru/articles/657657/" },
                        { name: "Репозиторий на github", href: "https://github.com/beldmian/yastation" }
                    ]}
                    tags={["Python", "API"]}
                />
                <ProjectCard
                    name="colorblinder"
                    description="Сервис фильтрации mpeg-dash стримов и VoD контента для людей с особенностями зрения."
                    links={[
                        { name: "Статья о проекте на habr", href: "https://habr.com/ru/articles/757240/" },
                        { name: "Репозиторий на github", href: "https://github.com/beldmian/colorblinder" }
                    ]}
                    tags={["Golang", "Next.js", "API", "Highload", "Accessibility", "Обработка видео"]}
                />
                <ProjectCard
                    name="invest_dashboard"
                    description="Сервис для мониторинга инвестиционного портфолио"
                    links={[
                        { name: "Репозиторий на github", href: "https://github.com/beldmian/invest_dashboard" }
                    ]}
                    tags={["Golang", "Prometheus", "Grafana", "API"]}
                />
                <ProjectCard
                    name="light"
                    description="Легкая event-система для golang"
                    links={[
                        { name: "Репозиторий на github", href: "https://github.com/beldmian/light" }
                    ]}
                    tags={["Golang"]}
                />
                <ProjectCard
                    name="avanpost-fingers"
                    description="Сервис распознавания отпечатков пальцев"
                    links={[
                        { name: "Репозиторий на github", href: "https://github.com/beldmian/avanpost-fingers" }
                    ]}
                    tags={["Golang", "API", "Highload", "C"]}
                />
                <ProjectCard
                    name="esgrs"
                    description="Система расчета и визуализации единого ESG-рейтинга компаний"
                    links={[
                        { name: "Репозиторий на github", href: "https://github.com/beldmian/esgrs" }
                    ]}
                    tags={["Golang", "API", "Svelte"]}
                />
            </Masonry>
        </Grid>
    )
}